<template>
  <div class="hello">
    <van-nav-bar title="我代理的订单" :fixed="true" placeholder />
    <van-tabs v-model="active"  >
      <!-- :sticky="true" :offset-top="50" -->


      <van-tab v-for="(item,index) in tabList" :key="index" :title="item.title" badge="1"  >
        <div class="tabListChild">
          <div class="tabListChildEdit">
            <van-search v-model="value" placeholder="请输入搜索关键词" />

            <van-button type="info" size="small" @click="popupToggle">批量操作</van-button>
          </div>
          <van-pull-refresh v-model="refreshing" @refresh="onRefresh" style="width:100%" >
            <van-list
              v-model="item.loading"
              :finished="item.finished"
              finished-text="没有更多了"
              @load="onLoad"
            >
            <div class="listParent" >
              <div class="listChild" >
                <div class="listChildTop" >
                  <p>高山铁观音新茶安溪铁观音茶叶清香型500g礼盒装</p>
                  <span>待确认</span>

                </div>
                <div class="listChildTime" >
                  <p>下单时间：2020/08/16  09:35</p>

                </div>
                <div class="listChildDes" ></div>



              </div>

            </div>
            
            
            </van-list>
          </van-pull-refresh>
        </div>
      </van-tab>
    </van-tabs>

    <!-- 弹出层 -->

    <van-popup v-model="popupShow" position="right">
      <div class="popupView">12345</div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      date: "",
      popupShow: false,
      value: "",
      active: 0,
      refreshing: false,
      tabList: [
        {
          title: "全部",
          type: 0,
          listData: [],
          pageNum: 1,
          finished: false,
          loading: true
        },
        {
          title: "待确认",
          type: 0,
          listData: [],
          pageNum: 1,
          finished: true,
          loading: true
        },
        {
          title: "待发货",
          type: 0,
          listData: [],
          pageNum: 1,
          finished: false,
          loading: true
        },
        {
          title: "已发货",
          type: 0,
          listData: [],
          pageNum: 1,
          finished: false,
          loading: true
        },
        {
          title: "已签收",
          type: 0,
          listData: [],
          pageNum: 1,
          finished: false,
          loading: true
        }
      ]
    };
  },
  methods: {
    formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onLoad() {},
    popupToggle() {
      let { popupShow } = this;
      this.popupShow = !popupShow;
    },
    onRefresh() {}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped >

.tabListChild {
  display: flex;
  align-items: center;
}
.ListView {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
}
.tabListChild {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 40rpx;
  box-sizing: border-box;
  align-items: center;
}
.tabListChildEdit {
  width: 100%;
  display: flex;
  align-items: center;
  background: #fff;
}
.popupView {
  width: 80vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 15px;
  box-sizing: border-box;
}
.listChild{
  width: 100%;
height: 102px;
background: #FFFFFF;
box-shadow: 0px 10px 50px 2px rgba(55,58,64,0.14);
border-radius: 10px;
margin-top: 20px;
padding: 15px;
box-sizing: border-box;

}
.listParent{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
}

.listChildTop{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 11px;
  
}
.listChildTop>p:nth-child(1){
font-size: 16px;
font-weight: 400;
color: #202020;
width: 200px;
display: flex;
flex-wrap: wrap;

}
.listChildTop>span{
font-size: 12px;
font-weight: 400;
color: #E96960;

}
.listChildTime{
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
  color: #7A7A7A;

}
</style>



